
:root { 
  --width: 28px
}

.grid { 
  display: grid;
  grid-template-columns: repeat(60, minmax(10px, 1fr));
  grid-template-rows: repeat(60);
  border-collapse: collapse;
  max-width: 1700px;
}

.cell { 
  max-width: 28px; 
  max-width: var(--width);
  height: 28px;
  height: var(--width);
  background-color: #EDF2F4;
  outline: 1px solid lightblue;
  display: flex;
}
  
.wall { 
  height: 28px; 
  height: var(--width);
  outline: 1px solid lightblue;;
  transition: background-color 0.5s ease-out;;
  -webkit-animation: grow 5s forwards;;
          animation: grow 5s forwards;
  background-color: black;
}

.startNode,
.endNode {
  width: 1.5vw;
  max-width: 28px;
  max-width: var(--width);
  height: 28px;
  height: var(--width);
  background-color: #EDF2F4;
  outline: 1px solid lightblue;
  font-size: min(1vw, 20px);
  background-image: url(data:image/png;base64,);
  background-position: center;
  background-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-repeat: no-repeat;

}

.startNode { 
  background-image: url(data:image/png;base64,);
  background-size: 20px;
  background-repeat: no-repeat;
}

.wall:before {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%) scale(0);
          transform: translate(-50%, -50%) scale(0);  
  border-radius: 50%;  
  background-color: black; 
  transition: -webkit-transform 0.5s ease-in-out; 
  transition: transform 0.5s ease-in-out; 
  transition: transform 0.5s ease-in-out, -webkit-transform 0.5s ease-in-out;  
  -webkit-animation: grow 5s forwards;  
          animation: grow 5s forwards;
}


.path { 
  height: 28px; 
  height: var(--width);
  outline: 1px solid lightblue;
  background-color: yellow;
  display: flex;
  align-items: center;
  justify-content: center;
}

.path{
  -webkit-animation-name: pathAnimation;
          animation-name: pathAnimation;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  outline: 1px solid #FFE134;
}

@-webkit-keyframes pathAnimation {
  0% {
    -webkit-transform: scale(.3);
            transform: scale(.3);
    background-color: #FE5A1C;
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: #FF9E28;
  }

  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    background-color: #FFE134;
  }
}

@keyframes pathAnimation {
  0% {
    -webkit-transform: scale(.3);
            transform: scale(.3);
    background-color: #FE5A1C;
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: #FF9E28;
  }

  100% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
    background-color: #FFE134;
  }
}



.visitedPath { 
  outline: 1px solid lightblue;
  height:28px;
  height:var(--width);
  display: flex;
  align-items: center;
  justify-content: center;
}

.visitedNodePurple{
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  -webkit-animation-name: visitedStartNodePurpleAnimation;
          animation-name: visitedStartNodePurpleAnimation;
  -webkit-animation-duration: 2.0s;
